export default {

  head() {
    return {

      link: [{ rel: 'stylesheet', href: 'https://cdn.jsdelivr.net/npm/bootstrap@5.0.2/dist/css/bootstrap.min.css' }],
    title: this.categoryTitle,
    meta: [
      { charset: 'utf-8' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1' },
      {
        hid: 'description',
        name: 'description',
        content: 'my website description'
      }
    ],
        }
  },

  data(){
    return {
      category: this.$route.params.id,
      title: 'Carregando...',
      titleEn: 'Loading...',
      titleEs: 'Carregando...',
      categoryTitle: undefined,
      language: this.$nuxt.$i18n.locale,
      data: undefined
    }
  },

  created() {
    this.executeData()
  },

  methods: {

    async executeData(){

      await this.$axios.post('/subcategory/category', { categoryId: this.category }).then((data) => {

        if (!data['data']['status']) {
          this.error = data['data']['data']['message']
        }

        this.titleEn = data['data']['data'][0]['category.titleEn'] || 'Not found'
        this.titleEs = data['data']['data'][0]['category.titleEs'] || 'No encontrado'
        this.categoryTitle = data['data']['data'][0]['category.title'] || 'NÃ£o encontrado'
        this.data = data['data']['data']
      })

    }

  }
}
